/**
 * @jest-environment jsdom
 */
import { renderBridgeHook } from './test-utils/index';
import { useToast } from '../src/hooks/index';

describe('Debug ToastProvider Context', () => {
  it('should provide toast context in bridge layer tests', () => {
    const { result } = renderBridgeHook(() => {
      const { showToast } = useToast();
      return { showToast };
    });

    expect(result.current).not.toBeNull();
    expect(typeof result.current.showToast).toBe('function');
  });

  it('should provide toast context without throwing error', () => {
    expect(() => {
      renderBridgeHook(() => {
        const { showToast } = useToast();
        return { showToast };
      });
    }).not.toThrow();
  });
});